  border:1px solid rgba(0,0,0,0.15);
}

.wrapper{
  margin-top: 10%;
}

pre {
  display: block;
  padding: 10px;
  margin: 0 0 10px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #abb2bf;
  background: #282c34;
  word-break: break-all;
  word-wrap: break-word;
  overflow: auto;
}
h1,h2,h3,h4,h5,h6{
  margin-top: 1em;
  /* margin-bottom: 1em; */
}
strong {
  font-weight: bold;
}

p > code:not([class]) {
  padding: 2px 4px;
  font-size: 90%;
  color: #c7254e;
  background-color: #f9f2f4;
  border-radius: 4px;
}
p img{
  /* 图片居中 */
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
}

